.Chat {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 90vh;
}

.App-header {
  background-color: rgba(0, 0, 0, 0.5);
  overflow: visible;
  width: 100%;
  color: #f4fdff;
  display: flex;
  justify-content: space-between;
}

.Messages-list {
  padding: 20px 0;
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  list-style: none;
  padding-left: 0;
  flex-grow: 1;
  overflow: auto;
}

.Messages-list::-webkit-scrollbar {
  display: none;
}

.Messages-message {
  display: flex;
  margin-top: 10px;
}

.Messages-message.currentMember {
  flex-direction: row-reverse;
  text-align: right;
}

.Message-content {
  display: inline-block;
}

.currentMember>.Message-content {
  align-items: flex-end;
}

.Messages-message>.avatar {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  display: inline-block;
  margin: 0 10px -10px;
}

.Message-content>.username {
  display: block;
  color: #f4fdff;
  font-size: 14px;
  padding-bottom: 4px;
}

.Message-content>.text {
  padding: 10px;
  max-width: 400px;
  margin: 0;
  border-radius: 12px;
  background-color: rgb(100, 237, 139);
  color: black;
  display: inline-block;
}

.currentMember>.Message-content .text {
  background-color: #f4fdff;
}

.message-area {
  display: flex;
  width: 100%;
  justify-content: space-between;
  max-width: 900px;
  margin: 0 auto 40px;

}


.message-area-input {
  padding: 5px;
  font-size: 16px;
  border-radius: 8px;
  border: 3px solid black;
  flex-grow: 1;
  background-color: #f4fdff;
}

.btn-send {
  padding: 5px 10px;
  font-size: 16px;
  background-color: #f4fdff;
  color: black;
  border: 3px solid black;
}

.btn-send {
  border-radius: 8px;
  margin-left: 10px;
}

.btn-send:hover {
  background-color: #857e7b;
  color: #f4fdff;
  transition: 0.2s ease-in-out;
}